ua, son fenómenos fundamentales para el sostenimiento de la vida en el planeta.")])])])])]),_c('div',{staticClass:"BG06 py-3 py-md-4",attrs:{"titulo":"Fase de fijación de carbono"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-5 mb-lg-0 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/img28_a.svg")}})]),_c('div',{staticClass:"col-lg-5"},[_c('p',[_vm._v("Tanto ATP como NADPH son fuentes de energía y poder reductor, respectivamente, para promover la síntesis de carbohidratos de carbono del dióxido de carbono, en azúcar simple; en este sentido, las reacciones iniciadas en el cloroplasto, encuentran continuidad en el citosol celular, generando en el mesófilo de las hojas, sacarosa y otras moléculas orgánicas; la sacarosa es exportada hacia otros tejidos como fuente de moléculas orgánicas. ")]),_c('p',[_vm._v("Lo anterior, sucede gracias a que la energía química almacenada, temporalmente, en las moléculas de ATP y NADPH, es transferida a las moléculas transportadoras y almacenadoras de energía, en las células de los organismos fotoautótrofos, formando un esqueleto hidrocarbonado desde donde se empiezan a construir diversas variedades de moléculas orgánicas.")]),_c('div',{staticClass:"BG05 p-4"},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Nota")]),_vm._v(": la incorporación del CO2 en compuestos orgánicos, es conocida, comúnmente, como fijación de carbono, sucediendo en el estroma de los cloroplastos.")])])])])])])],1),_c('separador'),_vm._m(19),_c('p',[_vm._v("El tejido vascular de las plantas, es característico de plantas superiores y atraviesan la totalidad del organismo, desde el extremo radicular, hasta el extremo de las hojas. Está conformado por el xilema y el floema y, con respecto a ellos, es necesario realizar un conjunto de precisiones para comprender su funcionamiento y relevancia en el desarrollo y sostenimiento de la planta.")]),_c('p',[_vm._v("En la siguiente imagen se puede observar co